@import './mixins.scss';

.table {
  @media (min-width: $breakpoint-xs) {
    width: 100%;
  }
  @media (min-width: $breakpoint-sm) {
    width: 90%;
  }
  @media (min-width: $breakpoint-md) {
    width: 80%;
  }
  @media (min-width: $breakpoint-lg) {
    width: 60%;
  }
  @media (min-width: $breakpoint-xl) {
    width: 40%;
  }
  min-width: 320px;
  border: 2px solid $tableBorderColor;
  display: flex;
  flex-direction: column;
  margin: auto;
  @include headerFooter();
  .tbody {
    @include row();
    .tr:nth-child(odd) {
      background-color: $oddChildColor;
    }
    .tr:nth-child(even) {
      background-color: $evenChildColor;
    }
    .tr {
      .td {
        P {
          word-wrap: break-word;
        }
        input {
          outline: none;
          border-style: solid;
          &:focus {
            border-color: $mainBlue;
          }
        }
        .fa-edit,
        .fa-trash-alt {
          cursor: pointer;
        }
        .fa-edit {
          margin-left: 6px;
          color: $mainBlue;
        }
        .fa-trash-alt {
          color: $mainRed;
        }
      }
    }
  }
  .tfooter {
    .pagination {
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
      padding: 10px 0;
      button {
        border-style: solid;
        outline: none;
        padding: 5px 10px;
        margin: 10px 5px;
        cursor: pointer;
      }
    }
  }
}
